export class Stock {
    id: string;
    name: string;
    region: string;
    state: string;
    address: string;
    country: string;
    contact: string;
    email: string;
    phone: string;
    managedBy: string;
    approver: string;

    constructor() {
      this.id = "";
      this.name = "";
      this.region = "";
      this.state = "";
      this.address = "";
      this.country = "";
      this.contact = "";
      this.email = "";
      this.phone = "";
      this.managedBy = "";
      this.approver = "";
    }
  }



